.site-info {
  border-top: 2px solid rgba(2, 2, 2, 0.733);
  padding: 1em;
  background-color: #031529;
  display: flex;
  flex-flow: column;
  align-items: left;
  font-size: 14px;
  color: white;
}
.site-info > p:hover {
  cursor: default;
}
.site-info > p {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}
.site-record-anchor {
  color: #ffffff;
  font-size: 12px;
}
